@import "../../inc/vars";
.wp-block-acrobits-pricing-table-block + .wp-block-acrobits-incentive-panel {
	.incentive_panel {
		background: none;
	}
}
.acro-pricing-table {
	padding: 64px 20px;
	width: 100%;
	max-width: 1440px;
	margin: 0 auto;
	color: #4F4B58;
	img {
		max-width: 100%;
		height: auto;
		vertical-align: top;
	}
	@media (min-width: $screen-sm){
		padding: 95px 28px 0;
	}
	&__title {
		text-align: center;
		margin-bottom: 34px;
		@media (min-width: $screen-sm){
			margin-bottom: 94px;
		}
		h1 {
			@include titleGrad();
			@include pop(400, 35, 1.24);
			letter-spacing: -0.7px;
			padding-bottom: 16px;
			@media (min-width: $screen-sm){
				@include pop(400, 44, 1.12);
				letter-spacing: -0.88px;
			}
		}
		h3 {
			@include titleGrad();
			@include pop(400, 18, 1.24);
		}
	}
	&__content {
		flex-wrap: wrap;
		justify-content: space-between;
		display: none;
		@media (min-width: $screen-md) {
			display: flex;
		}
		&__details {
			width: calc(25% - 15px);
			position: relative;
			@media (max-width: 1024px) {
				width: 100%;
				margin-bottom: 40px;
			}
			.acro-pricing-table__plan_info {
				border-radius: 12px;
				background: linear-gradient(180deg, #EEEFF2 0%, #FFF 100%);
				box-shadow: 0px 9px 33px 0px rgba(162, 165, 168, 0.20), 0px 1.127px 4.132px 0px rgba(162, 165, 168, 0.41);
				overflow: hidden;
				display: none;
				position: absolute;
				left: 0;
				top: 0;
				width: 100%;
				&.active {
					display: flex;
					&.up {
						animation: acroSlideInUp 0.8s ease-in-out;
					}
					&.down {
						animation: acroSlideInDown 0.8s ease-in-out;
					}
				}
				&__inner {
					display: flex;
					flex-direction: column;
					align-items: flex-end;
					&__content {
						padding: 34px 24px 0 24px;
						@media (min-height: 900px){
							padding: 44px 44px 0 44px;
						}
					}
					&__icon {
						margin-bottom: 24px;
						max-width: 50px;
						@media (min-height: 900px){
							max-width: 100%;
						}
					}
					&__title {
						@include titleGrad();
						@include pop(400, 23, 1.24);
						letter-spacing: -0.02em;
						margin-bottom: 14px;
						@media (min-height: 900px){
							font-size: 35px;
							margin-bottom: 24px;
						}
					}
					&__description {
						@include fig(400, 16, 1.25);
						margin-bottom: 30px;
					}
					& > img {
						margin-bottom: -7px;
						@media (min-height: 900px){
							margin-bottom: 0;
						}
					}
				}
			}
		}
		&__table_wrapper {
			width: 75%;
			border-radius: 12px;
			background: var(--White-Heading, linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%, #FFF 0.01%, rgba(255, 255, 255, 0.60) 100%));
			@media (max-width: 1024px) {
				width: 100%;
			}
		}
		&__table {
			display: flex;
			align-items: flex-start;
		}
		&__col {
			flex: 1;
			.list-item {
				&:first-child {
					height: 98px;
				}
			}
		}
		&__features {
			min-width: 33%;
			@media (min-height: 900px){
				min-width: 200px;
			}
			.list-item {
				padding: 5px 24px;
				height: 43px;
				display: flex;
				justify-content: center;
				text-align: right;
				flex-direction: column;
				@media (min-height: 900px){
					height: 58px;
				}
				&:nth-child(even){
					background: rgba(0, 0, 0, 0.02);
				}
				&__title {
					display: flex;
					align-items: center;
					width: 100%;
					justify-content: flex-end;
					@include fig(400, 15, 1.06);
					color: #1E1C21;
					img {
						margin-left: 10px;
					}
				}
			}
		}
		&__plan {
			opacity: 0.6;
			box-shadow: 0px 2px 12px 0px rgba(0, 0, 0, 0.09);
			cursor: pointer;
			.list-item {
				.listContent {
					@include pop(400, 16, 1.13);
					@include titleGrad();
					height: 100%;
					text-align: center;
					padding: 28px 14px 10px;
					position: relative;
					display: flex;
					flex-direction: column;
					justify-content: center;
					@media (min-height: 900px){
						font-size: 18px;
						padding: 28px 24px 10px;
					}
					img{
						position: absolute;
						left: 50%;
						top: 30px;
						transform: translate(-50%, -50%);
						filter: drop-shadow(0px 0px 10px rgba(112, 72, 199, 0.40));
						margin-bottom: 15px;
						transition: 0.5s;
						opacity: 0;
						max-width: 50px;
						@media (min-height: 900px){
							max-width: 100%;
						}
					}
				}
			}
			.features {
				.checkbox-wrap {
					height: 43px;
					display: flex;
					flex-direction: column;
					align-items: center;
					justify-content: center;
					@media (min-height: 900px){
						height: 58px;
					}
					&:nth-child(odd){
						background: var(--Constants-Black-8, rgba(0, 0, 0, 0.08));
						background: rgba(0,0,0,.02);
					}
					span {
						display: block;
						width: 20px;
						height: 20px;
						border-radius: 50%;
						border: 2px solid var(--Constants-Black-8, rgba(0, 0, 0, 0.08));
						position: relative;
						&:before {
							content: '';
							display: block;
							width: 8px;
							height: 2px;
							left: 50%;
							top: 50%;
							transform: translate(-50%, -50%);
							position: absolute;
							background: rgba(0, 0, 0, 0.08);
						}
						&.checked {
							border-color: $green;
							&:before {
								height: 8px;
								border-radius: 50%;
								background: $green;
							}

						}
					}
				}
			}
			.before-price {
				padding: 7px 10px;
				color: #777184;
				@include fig(400, 10, 16px);
				letter-spacing: 0.5px;
				text-transform: uppercase;
				text-align: center;
				border-top: 0.5px solid var(--Constants-Black-8, rgba(0, 0, 0, 0.08));
			}
			.price-wrap {
				padding: 19px 10px;
				display: flex;
				align-items: center;
				justify-content: center;
				color: #768093;
				@include fig(400, 10, 16px);
				letter-spacing: 0.7px;
				text-transform: uppercase;
				@media (min-height: 900px){
					padding: 19px 20px;
				}
				.price {
					color: $purple;
					@include pop(400, 29, 1.24);
					padding: 0 8px;
					@media (min-height: 900px){
						font-size: 35px;
					}
				}
			}
			&:nth-child(2) {
				.list-item {
					.listContent {
						@media (min-height: 900px){
							font-size: 20px;
						}
					}
				}
			}
			&.active {
				opacity: 1;
				.list-item {
					.listContent {
						img {
							opacity: 1;
							top: 0;
						}
					}
				}
			}
		}
	}
	&__content__mobile {
		display: flex;
		gap: 15px;
		padding: 30px 0;
		align-items: flex-start;
		@media (min-width: $screen-md){
			display: none;
		}
		overflow-x: auto;
		&__plan {
			border-radius: 12px;
			flex: 0 0 calc(100vw - 75px);
			background: var(--Constants-White-100, #FFF);
			box-shadow: 0px 2px 4px 0px rgba(7, 4, 146, 0.10), 0px 24px 60px 0px rgba(6, 47, 125, 0.05), 0px 12px 24px 0px rgba(27, 59, 119, 0.05);
			overflow: hidden;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			.plan_top {
				padding-top: 24px;
				position: relative;
				flex: 1;
				.plan {
					&__icon {
						text-align: center;
						margin-bottom: 4px;
					}
					&__title {
						@include titleGrad();
						@include pop(400, 23, 1.36);
						padding: 0 20px 24px 20px;
						border-bottom: 0.5px solid var(--Constants-Black-8, rgba(0, 0, 0, 0.08));
						text-align: center;
					}
					&__description {
						padding: 30px 32px 20px;
						@include fig(400, 16, 1.5);
					}
				}
				.features {
					padding: 0 32px 20px;
					display: none;
					.feature {
						&__item {
							color: #1E1C21;
							display: flex;
							align-items: flex-start;
							gap: 8px;
							@include fig(400, 16, 1.5);
							margin-bottom: 12px;
							svg {
								flex: 0 0 20px;
								width: 20px;
								margin-top: 2px;
							}
						}
					}
				}
				.features-toggle {
					display: flex;
					padding: 10px 8px 10px 20px;
					justify-content: space-between;
					align-items: center;
					gap: 8px;
					align-self: stretch;
					border-radius: 10px;
					border: 1.2px solid var(--Secondary-500, #1E8F69);
					margin: 0 32px;
					color: #1E8F69;
					@include pop(500, 16, 1);
					letter-spacing: 0.16px;
					svg {
						width: 24px;
						transition: .3s;
					}
					&.active {
						svg {
							transform: rotate(180deg);
						}
					}
				}
				.before-price {
					color: #777184;
					@include fig(400, 12, 1.33);
					letter-spacing: 0.6px;
					text-transform: uppercase;
					padding: 24px 15px 12px;
					text-align: center;
					margin-top: auto;
				}
			}
			.plan_bottom {
				background: #F7FBFB;
				padding: 14px 24px 24px;
				.price-wrap {
					display: flex;
					align-items: center;
					justify-content: center;
					color: #768093;
					@include fig(400, 14, 14px);
					letter-spacing: 0.7px;
					text-transform: uppercase;
					padding-bottom: 15px;
					.price {
						color: $purple;
						@include pop(400, 44, 1.12);
						padding: 0 8px;
					}
				}
			}
			&:first-child {
				.plan__title {
					font-size: 29px !important;
				}
			}
		}
	}
	.buy-btn {
		width: 100%;
		display: flex;
		justify-content: flex-end;
		padding: 14px;
		.btn {
			width: 67%;
			height: 37px;
			font-size: 19px;
			font-weight: 500;
			letter-spacing: 0.24px;
			display: none;
			@media (min-height: 900px){
				height: 56px;
				font-size: 24px;
				width: calc(100% - 185px);
			}
			&.active {
				display: flex;
			}
		}
	}
	.btn {
		@include greenButton();
	}
}

@keyframes acroSlideInUp {
	0% {
		top: 20%;
	}
	100% {
		top: 0;
	}
}

@keyframes acroSlideInDown {
	0% {
		top: -20%;
	}
	100% {
		top: 0;
	}
}